.record_game_button {
    margin: 3%;
    min-width: 150px;
    border-radius: 20px;
}

.selected_game {
    background: -webkit-linear-gradient(290deg, teal, #17a2b8);
    background: -o-linear-gradient(290deg, teal, #17a2b8);
    background: linear-gradient(160deg, teal, #17a2b8);
    color: white;
}

.unselected_game {
    color: teal;
    border-style: solid;
    border-color: teal;
    background-color: white;
    border-width: 2px;
}
.game_action_icon {
    margin-left: 10px;
}


